.root {
  margin: 20px auto;
  max-width: 1000px;
}

.appHeader {
  height: 56px;
  display: flex;
  align-items: center;
  padding-left: 5vw;
  background-color: var(--primary-color);
  box-shadow: 0px 3px 5px 0px rgba(0,0,0,0.4);
}

.appTitle {
  margin: 0;
  font-size: 1.6em;
  color: var(--accent-color-dark);
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0;
  transition: opacity 0.3s ease;
  background-color: rgba(0,0,0,0.4);
}

.overlayEnter {
  opacity: 1;
}

.modalContainer {
  transition-duration: 0.3s;
  transition-timing-function: ease;
  transform: translateY(100%);
}

.modalContainerEnter {
  transform: translateY(0);
}


@media(min-width: 650px) {

  .modalContainer {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    pointer-events: none;
    transform: scale(0, 0);
    opacity: 0;
    transition-duration: 0.3s;
    transition-timing-function: ease;
    transition-property: transform, opacity;
  }

  .modalContainerEnter {
    opacity: 1;
    transform: scale(1, 1);
  }
}

